@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.loader {
  display: flex;
  justify-content: center;
  margin-top: 50px; }
  .loader .lds-dual-ring {
    display: inline-block;
    width: 64px;
    height: 64px; }
  .loader .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #2196F3;
    border-color: #2196F3 transparent #2196F3 transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite; }

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.ticket {
  margin-bottom: 20px;
  background-color: #fff;
  padding: 20px 50px 20px 20px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px; }
  .ticket__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px; }
  .ticket__price {
    font-size: 24px;
    line-height: 24px;
    color: #2196F3; }
  .ticket__way {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px; }
  .ticket__logo {
    font-size: 0; }
    .ticket__logo img {
      max-width: 100px; }
  .ticket__title {
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #A0B0B9; }
  .ticket__descr {
    font-size: 14px;
    line-height: 21px;
    font-weight: 600;
    color: #4A4A4A; }
  .ticket__route {
    min-width: 92px; }
  .ticket__lenght {
    min-width: 63px; }
  .ticket__stops {
    min-width: 92px;
    display: flex;
    flex-direction: column;
    align-items: flex-start; }

.no-tickets {
  display: block;
  padding: 20px;
  border-radius: 5px;
  background-color: #fff;
  text-align: center;
  font-weight: 600px; }

.filters {
  background: #FFFFFF;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 10px 0 10px 0;
  margin-right: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .filters__title {
    margin: 0;
    padding: 10px 20px 10px 20px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #4A4A4A; }
  .filters__item {
    padding: 10px 20px; }
  .filters__item:hover {
    background: #F1FCFF; }
  .filters input[type="checkbox"] + label {
    cursor: url(data:image/png;base64,), pointer;
    font-size: 13px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #4A4A4A; }
  .filters input[type="checkbox"] + label::before {
    content: "";
    display: inline-block;
    height: 20px;
    width: 20px;
    border: 1px solid #9ABBCE;
    border-radius: 2px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center; }
  .filters input[type="checkbox"]:checked + label::before {
    border: 1px solid #2196F3;
    content: url(data:image/jpeg;base64,); }

.tabs {
  display: flex;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .tabs__item {
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    background: #FFFFFF;
    color: black;
    padding: 15px 66px;
    cursor: url(data:image/png;base64,), pointer; }
  .tabs .active {
    background: #2196F3;
    color: #fff; }

.main {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0; }
  .main .logo {
    margin-bottom: 50px; }
  .main__inner {
    display: flex;
    align-items: flex-start; }

body {
  background: #E5E5E5;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-style: normal; }

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0; }

